// Auto-generated by gulp-svg-sprite
 
%sprite {
    content: '';
    background: url("../sass/sass/inc/svg-sprite.svg") no-repeat;
    background-size: 244px 186px;
    display: inline-block;
    .no-svg & {
        background-image: url("../sass/sass/inc/svg-sprite.png");
    }
}

@mixin sp__Logo {
    @extend %sprite;
    background-position: -5px -5px;
    width: 123px;
    height: 52px;
}
